import React from "react"
import {Button, Form, Row, Col} from "react-bootstrap"
import "./ContactForm.scss"

export default function ContactForm() {

	const [name, setName] = React.useState("")
	const [email, setEmail] = React.useState("")
	const [msg, setMsg] = React.useState("")

	function handleNameChange(e) {setName(e.target.value)}
	function handleEmailChange(e) {setEmail(e.target.value)}
	function handleMsgChange(e) {setMsg(e.target.value)}

	function handleSubmit(e) {
		e.preventDefault()
		let formdata = new FormData();
		formdata.append("entry.720677371", name);
		formdata.append("entry.529120879", email);
		formdata.append("entry.34638534", msg);
		const requestOptions = {
			method: 'POST',
			body: formdata,
			redirect: 'follow',
			mode: 'no-cors',
		};
		fetch("https://docs.google.com/forms/d/e/1FAIpQLScY-PL1-MJLgSWZktSpGU_1faqdpwm3XPW5LGInd3lZCBmT4Q/formResponse", requestOptions)
			// .then(response => response.text())
			// .then(result => console.log(result))
			// .catch(error => console.log('error', error));
	
		window.alert("Your message was sent. Thank you!")
		setName("")
		setEmail("")
		setMsg("")
	}

	return(
		<div className="bootstrap-container">
			<Form onSubmit={handleSubmit}>
				<Form.Group as={Row}>
					<Form.Label column sm={2}>Name</Form.Label>
					<Col sm={10}>
						<Form.Control
							required
							type="text"
							placeholder="Name"
							value={name}
							onChange={handleNameChange} />
					</Col>
				</Form.Group>
				<Form.Group as={Row}>
					<Form.Label column sm={2}>Email</Form.Label>
					<Col sm={10}>
						<Form.Control
							required
							type="email"
							placeholder="Email"
							value={email}
							onChange={handleEmailChange} />
					</Col>
				</Form.Group>
				<Form.Group as={Row}>
					<Form.Label column sm={2}>Message</Form.Label>
					<Col sm={10}>
						<Form.Control
							required
							as="textarea"
							rows={4}
							placeholder="Message"
							value={msg}
							onChange={handleMsgChange} />
					</Col>
				</Form.Group>
				<Form.Group as={Row}>
					<Col sm={{span: 10, offset: 2}}>
						<Button type="submit">Send</Button>
					</Col>
				</Form.Group>
			</Form>
		</div>
	)

}