import React from "react"
import { LazyPlotly } from '../../../../src/components/Plotly'

import * as j from './df_date.json'

const data = j.data
const reg_date = data.map((x => x[4]))
const count = data.map((x => x[1]))
const score = data.map((x => x[2]))
const year = data.map((x => x[3]))

const boxPlotSeriesOptions = {
	type: 'box',
	boxpoints: 'all',
	jitter: 0.6,
	pointpos: -2,
	marker: {
		size: 3,
	},
	hoverinfo: 'text+y',
	hovertemplate: '%{text}<br /> %{y:.0f} <extra></extra>'
}

const catname = ['', '', '', '', '']

const boxPlotDataAll = [0, 0.25, 0.5, 0.75, 1].map((s, j) => {
	return {
		name: catname[j],
		y: count.filter((x, i) => score[i] === s),
		text: reg_date.filter((x, i) => score[i] === s),
		...boxPlotSeriesOptions
	}
})

const boxPlotDataEx2020 = [0, 0.25, 0.5, 0.75, 1].map((s, j) => {
	return {
		name: catname[j],
		y: count.filter((x, i) => (score[i] === s) && (year[i] < 2020)),
		text: reg_date.filter((x, i) => (score[i] === s) && (year[i] < 2020)),
		...boxPlotSeriesOptions
	}
})

export default function YearFilter() {

	const [showAll, setShowAll] = React.useState(true)

	let boxPlot = {
		data: showAll ? boxPlotDataAll : boxPlotDataEx2020,
		layout: {
			showlegend: false,
			title: {
				text: ' ',
			},
			xaxis: {
				title: {
					text: '',
				},
				fixedrange: true,
			},
			yaxis: {
				title: {
					text: ' ()',
				},
				range: [100, 500],
				fixedrange: true,
			},
			hoverlabel: {
				font: {
					color: `white`,
				},
			},
			hovermode: 'closest',
		}
	}

	function handle2020(e) {
		setShowAll(e.target.checked)
	}

	return(
		<React.Fragment>
			<LazyPlotly {...boxPlot} />
			<div style={{textAlign: `center`, fontSize: `0.8rem`}}>
				<label>
					<input type="checkbox" onChange={handle2020} defaultChecked={showAll} />
					 2563
				</label>
			</div>
		</React.Fragment>
	)

}