import {range} from 'lodash'

function foo(x) {
	return 87.915 / (6.4 + (87.915 / (x / 3.6))) * 3.6
}

const x = range(200, 0.05)

export default {
	data: [
		{
			x: x,
			y: x.map(foo),
			type: 'scatter',
			mode: 'lines',
			marker: {color: '#D76D07'},
			hovertemplate: ' %{x:.0f} ./.<br /> %{y:.0f} ./.<extra></extra>'
		},
	],
	layout: {
		showlegend: false,
		title: {
			text: ' 100 ',
			xref: 'paper',
			x: 0.5,
			xanchor: 'center'
		},
		xaxis: {
			title: {
				text: ' (./.)',
				// font: {
				// 	size: 14,
				// }
			},
		},
		yaxis: {
			title: {
				text: ' (./.)',
				// font: {
				// 	size: 14,
				// }
			},
		},
		annotations: [
			{
				x: 76.175,
				y: foo(76.175),
				xref: 'x',
				yref: 'y',
				text: '<br />',
				showarrow: true,
				ax: 30,
				ay: 30,
				arrowhead: 7,
				arrowcolor: '@text'
			},
			{
				x: 177,
				y: foo(177),
				xref: 'x',
				yref: 'y',
				text: ' 177 ./.',
				showarrow: true,
				ax: -30,
				ay: -30,
				arrowhead: 7,
				arrowcolor: '@text'
			}
		],
		hoverlabel: {
			font: {
				color: `white`,
			},
		},
	}
}