// https://janosh.dev/blog/gatsby-interactive-plots

import React from 'react'
import Loadable from 'react-loadable'
import Highcharts from 'highcharts';
import highchartsData from "highcharts/modules/data"
import highchartsMap from "highcharts/modules/map"
import useDarkMode from 'use-dark-mode';
import {cloneDeep, merge} from 'lodash'
import styles from '../styles/_variables.module.scss'

if (typeof Highcharts === 'object') {
	highchartsData(Highcharts)
	highchartsMap(Highcharts)
}

const LoadPlotly = Loadable({
	loader: () => import(`react-plotly.js`),
	loading: ({ timedOut }) =>
		timedOut ? (
			<blockquote>Error: Loading Plotly timed out.</blockquote>
		) : (
			<div>Loading...</div>
		),
	timeout: 30000,
})

const LoadHighcharts = Loadable({
	loader: () => import(`highcharts-react-official`),
	loading: ({ timedOut }) =>
		timedOut ? (
			<blockquote>Error: Loading Highcharts timed out.</blockquote>
		) : (
			<div>Loading...</div>
		),
	timeout: 30000,
})

function getColors(darkMode) {
	let colors = {}
	colors.text = darkMode ? styles.x_dark_text_color : styles.x_light_text_color
	colors.background = darkMode ? styles.x_dark_background_color : styles.x_light_background_color
	colors.subtle = darkMode ? styles.x_dark_subtle_color : styles.x_light_subtle_color
	colors.primary = darkMode ? styles.x_dark_primary_color : styles.x_light_primary_color
	colors.secondary = darkMode ? styles.x_dark_secondary_color : styles.x_light_secondary_color
	
	Object.keys(colors).forEach(key => {
		if (colors[key].length === 4) {
			let tmp = colors[key][0] + colors[key][1] + colors[key][1] + colors[key][2] + colors[key][2] + colors[key][3] + colors[key][3]
			colors[key] = tmp
		}
	});

	colors.gridline = colors.subtle + "33"
	colors.zeroline = colors.subtle + "66"
	return colors
}

export const LazyHighcharts = ( {constructorType, options, refID} ) => {

	const darkMode = useDarkMode(false)
	const colors = getColors(darkMode.value)

	const buttonTheme = {
		stroke: `rgba(0, 0, 0, 0)`,
		r: 5,
		fill: colors.gridline,
		style: {
			// color set in main.scss
		},
		states: {
			hover: {
				fill: colors.zeroline,
			}
		}
	}

	const defOptions = {
		chart: {
			backgroundColor: `rgba(0, 0, 0, 0)`,
			type: 'spline',
			resetZoomButton: {
				theme: buttonTheme
			},
		},
		mapNavigation: {
			buttonOptions: {
				theme: buttonTheme,
			},
		},
		colors: [colors.secondary].concat(["#7cb5ec", "#434348", "#90ed7d", "#f7a35c", "#8085e9", "#f15c80", "#e4d354", "#2b908f", "#f45b5b", "#91e8e1"]),
		xAxis: {
			gridLineColor: colors.gridline,
			lineColor: colors.zeroline,
			tickColor: colors.zeroline,
			labels: {
				style: {color: colors.text, fontSize: 14, fontFamily: styles.font_body},
			},
			title: {
				style: {color: colors.text, fontSize: 14, fontFamily: styles.font_body},
			},
		},
		yAxis: {
			gridLineColor: colors.gridline,
			lineColor: colors.zeroline,
			tickColor: colors.zeroline,
			labels: {
				style: {color: colors.text, fontSize: 14, fontFamily: styles.font_body},
			},
			title: {
				style: {color: colors.text, fontSize: 14, fontFamily: styles.font_body},
			},
		},
		title: {
			text: 'My chart',
			style: {color: colors.text, fontSize: 18, fontFamily: styles.font_heading}
		},
		legend: {
			itemStyle: {color: colors.text + "99", fontSize: 14, fontFamily: styles.font_body, fontWeight: `normal`},
			itemHoverStyle: {color: colors.text}
		},
		tooltip: {
			borderWidth: 0,
			shadow: false,
			backgroundColor: colors.background + 'cc',
			borderRadius: 5,
			style: {color: colors.text, fontFamily: styles.font_body},
			xDateFormat: '%Y-%m-%d',
		}
	};

	Highcharts.dateFormats = {
		q: function (timestamp) {
			const date = new Date(timestamp)
			return (Math.floor(date.getUTCMonth() / 3) + 1);
		}
	};

	return <LoadHighcharts
						highcharts={Highcharts}
						constructorType={constructorType}
						options={merge(defOptions, processColors(cloneDeep(options), colors))}
						ref={refID} />

}

function processColors(obj, colors) {
	if (obj == null)
		return
	for (const [k, v] of Object.entries(obj)) {
		if (v == null)
			continue
		if (typeof v === "object") {
			obj[k] = processColors(v, colors)
		}
		else if (v[0] === '$') { // styles
			obj[k] = styles[v.substring(1)]
		}
		else if (v[0] === '@') { //colors
			if (v.substring(v.length - 3, v.length - 2) === '@')
				obj[k] = colors[v.substring(1, v.length - 3)] + v.substring(v.length - 2)
			else
				obj[k] = colors[v.substring(1)]
		}
	}
	return obj
}

export const LazyPlotly = ( {layout, style, config, ...rest} ) => {
	
	const darkMode = useDarkMode(false)
	// const colors = getColors(darkMode.value)

	const [colors, setColors] = React.useState(getColors(darkMode.value))
	
	const axisDefaults = {
		gridcolor: colors.gridline,
		zerolinecolor: colors.zeroline,
		title: {
			font: {
				size: 14,
			},
		},
	}

	const defLayout = {
		paper_bgcolor: `rgba(0, 0, 0, 0)`,
		plot_bgcolor: `rgba(0, 0, 0, 0)`,
		autosize: true,
		title: {
			font: {
				family: styles.font_heading,
				size: 18,
			},
			xref: 'paper',
			x: 0.5,
			xanchor: 'center',
		},
		font: {
			family: styles.font_body,
			color: colors.text,
		},
		xaxis: cloneDeep(axisDefaults),
		yaxis: cloneDeep(axisDefaults),
		xaxis2: cloneDeep(axisDefaults),
		xaxis3: cloneDeep(axisDefaults),
		xaxis4: cloneDeep(axisDefaults),
		yaxis2: cloneDeep(axisDefaults),
		yaxis3: cloneDeep(axisDefaults),
		yaxis4: cloneDeep(axisDefaults),
		hoverlabel: {
			font: {
				family: styles.font_body,
				color: `white`,
			},
			bordercolor: 'transparent',
		},
	}

	const defStyle = {
		width: `100%`,
	}
	const defConfig =  {
		displayModeBar: false,
		showTips: false,
	}

	// let finalLayout = merge(layout, defLayout)

	React.useEffect(() => {
		setColors(getColors(darkMode.value))		
	}, [darkMode.value])

	return (
		<LoadPlotly
			layout={merge(defLayout, processColors(cloneDeep(layout), colors))}
			style={{...merge(defStyle, style)}}
			config={{...merge(defConfig, config)}}
			{...rest}
			useResizeHandler
			misc={darkMode.value}
		/>
	)
}